import React from 'react';
import 'react-circular-progressbar/dist/styles.css';
import { Grid, Label } from 'semantic-ui-react';

const colors = [
    { color: 'grey', value: 'React Js' },
    { color: 'grey', value: 'Next js' },
    { color: 'grey', value: 'Vue JS' },
    { color: 'grey', value: 'Redux' },
    { color: 'grey', value: 'React Native' },
    { color: 'grey', value: 'Express JS' },
    { color: 'grey', value: 'Laravel' },
    { color: 'grey', value: 'Git' },
    { color: 'grey', value: 'TypeScript' },
    { color: 'grey', value: 'SCSS & HTML' },
    { color: 'grey', value: 'Graphic Design' },
    // { color: 'grey', value: 'Linux' },
]
const Skill = () => (
    <Grid style={{ backgroundColor: '#fff', boxShadow: '0px 3px 5px rgba(0, 0, 0, 0.5)' }} padded>
        <Grid.Row >
            <Grid.Column width={3} />
            <Grid.Column width={10} textAlign='center' >
                {colors.map(({ value }, i) => (
                    <Label style={{margin:'.5em'}} size='huge' key={i}> {value}</Label>
                ))}
            </Grid.Column>
            <Grid.Column width={3} />
        </Grid.Row>
    </Grid>
);
export default Skill;


